import { useCallback, useEffect, useState } from "react"
import { get } from "../../Functions/Api"
import { useParams } from "react-router-dom"

const FotoKK = () => {
    const { no_kk } = useParams()

    const [FotoKK, setFotoKK] = useState<DataT>()

    useEffect(() => {
        const getFotoKK = async () => {
            const res = await get(`data-keluarga/foto-kk/${no_kk}`)
            if (res.ok) setFotoKK(res.data)
            return res
        }
        getFotoKK()
    })

    return (
        <div className="text-center">
            <img src={`data:image/jpeg;base64,${FotoKK?.data}`} alt="" />
        </div>
    )
}

export default FotoKK
